      description:
        '<p>Migration &amp; Identity - Conversations through experience<br/>with Tuğba Yalçınkaya and İrem Aydın</p> <p>ABOUT<br/>In Germany, nearly every 4th person has an immigration background. Essentially, we are already a land of immigration,” those were the words of chancellor Merkel in 2015. But still there are a lot of conversations about who is allowed to be German and who is not.</p> <p>As a company, ThoughtWorks not only bases its success on its diverse workforce, but also we are committed to becoming aware of our own privilege and striving to see the world from the perspective of the oppressed, the powerless, and the invisible.</p> <p>With this event series, we want to raise awareness, help to decrease discrimination, and to sensitize people without migration backgrounds to the challenges faced by people with migration backgrounds here in Germany. We are inviting speakers to share their voices and experiences in support of these goals. We hope to challenge our cosmopolitan (oftentimes color-blind) worldview that blocks us from gaining a real understanding of challenges experienced by the various vibrant communities inside of this country.</p> <p>THE TALK<br/>Migration from Turkey to Germany has a long history; starting with the first migration flow of the “guest workers” in the early 1960s up to the latest “New Wave” migration. These migration flows have created different diaspora communities in Germany. Within the framework of the event we will have a closer look at Turkey diaspora by focusing on questions as e.g.:<br/>How did Turkey diaspora shape itself in recent years and how do these flows differ themselves from each other and why? How does political polarization in Turkey affect diaspora in Germany and the position of Germany in between. While dealing with these issues İrem Aydın, as part of the “New Wave” migration, and Tuğba Yalçınkaya, as part of the 3rd generation of “guest workers”, will give insights into their personal perspectives on how they perceive themselves and how social encounters and confrontations shape their identities.</p> <p>The talk is a joint event of ThoughtWorks and Maviblau!<br/>Check out more info about Maviblau here:',
